<template>
  <div>
    <div>
      <div class="page-title-cont">
        <h1 class="wp-heading-inline">WPCal.io - Event Types</h1>
        <router-link to="/event-type/add" class="page-title-action"
          >+ Create New Event Type</router-link
        >
        <AdminHeaderRight />
      </div>
      <div class="service-list-cont">
        <div
          class="service-list-single"
          :class="{
            ['clr-' + service.color]: true,
            inactive: service.status == -1
          }"
          v-for="(service, index) in service_list"
          :key="index"
        >
          <h4>{{ service.name }}</h4>
          <span
            :class="{
              ['service_overflow_options_' + service.id]: true
            }"
          >
            <a
              class="overflow-btn"
              style="float:right"
              @click="toggle_service_menu(service.id)"
            ></a>
            <span
              class="overflow-options"
              :class="{
                hidden: menu_open_service_id != service.id
              }"
            >
              <div>
                <a
                  :href="'#/event-type/edit/' + service.id"
                  @click="toggle_service_menu(service.id)"
                  >Edit this Event Type</a
                >
              </div>
              <div>
                <a
                  style="color: rgb(232, 70, 83);"
                  @click="may_show_service_status_change_dialog(-2, service)"
                  >Delete this Event Type</a
                >
              </div>
              <hr />
              <div style="display:flex">
                <span style="color: #7c7d9c !important; font-size: 13px;"
                  >On/Off</span
                >
                <OnOffSlider
                  style="margin-left:auto;"
                  v-if="service.status == 1 || service.status == -1"
                  :name="service.id"
                  v-model="service.status"
                  true_value="1"
                  false_value="-1"
                  @prop_slider_value_changed="
                    may_show_service_status_change_dialog($event, service)
                  "
                />
              </div>
            </span>
          </span>

          <div class="et-meta">
            {{ service.duration | mins_to_str }},
            {{ service.relationship_type | relationship_type_str }}
          </div>
          <div
            v-if="service.post_details.link"
            style="display:flex; margin-top:10px;"
          >
            <a
              class="view-booking-page"
              :href="service.post_details.link + '#/booking'"
              target="_blank"
              >View Booking Page</a
            >
            <div style="margin-left:auto;">
              <img
                :src="$store.getters.get_current_admin_details.profile_picture"
                width="30"
                style="border-radius:50%"
                :title="
                  'This event type is available only for ' +
                    $store.getters.get_current_admin_details.display_name +
                    '.'
                "
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <v-dialog />
  </div>
</template>

<script>
import OnOffSlider from "@/components/form_elements/OnOffSlider.vue";

import axios from "axios";

export default {
  components: {
    OnOffSlider
  },
  data() {
    return {
      document_title: "Event Types",
      service_list: {},
      menu_open_service_id: null
    };
  },
  methods: {
    load_page() {
      let action = "get_services_of_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        dummy__: ""
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          if (
            response.data &&
            response.data[action].hasOwnProperty("service_list")
          ) {
            this.service_list = response.data[action].service_list;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    toggle_service_menu(service_id) {
      this.menu_open_service_id =
        this.menu_open_service_id == service_id ? null : service_id;
      if (this.menu_open_service_id) {
        setTimeout(() => {
          this.on_click_outside(
            ".service_overflow_options_" + service_id,
            () => {
              if (this.menu_open_service_id == service_id) {
                this.menu_open_service_id = null;
              }
            }
          );
        }, 100);
      }
    },
    may_show_service_status_change_dialog(status, service) {
      const allowed_status = [1, -1, -2, "1", "-1", "-2"];
      if (allowed_status.indexOf(status) == -1) {
        return;
      }

      if (status != 1) {
        this.toggle_service_menu(service.id);
      }

      let delete_disable_str =
        "<ul class='bull'><li>Exisiting bookings will NOT be affected.</li><li>New booking and rescheduling will NOT be allowed.</li><li>Cancellation will be allowed.</li></ul>";

      let status_action_str = "turn on";
      let button_str = "Turn On";
      let detail_str = "";
      if (status == -1) {
        status_action_str = "turn off";
        button_str = '<span style="color: #ff7600;">Turn Off</span>';
        detail_str = delete_disable_str;
      } else if (status == -2) {
        status_action_str = "delete";
        button_str = '<strong style="color: #ff0000;">Delete</strong>';
        detail_str = delete_disable_str;
      } else if (status == 1) {
        this.change_service_status(status, service);
        return;
      }

      this.$modal.show("dialog", {
        title: "Alert",
        text:
          "Are you sure, do you want to " +
          status_action_str +
          " the " +
          "<strong> " +
          service.name +
          "</strong> " +
          " event type?<br > " +
          detail_str,
        buttons: [
          {
            title: button_str,
            handler: () => {
              this.change_service_status(status, service);
              this.$modal.hide("dialog");
            }
          },
          {
            title: "Cancel",
            default: true, // Will be triggered by default if 'Enter' pressed.
            handler: () => {
              this.load_page();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    change_service_status(status, service) {
      let wpcal_request = {};

      let action_update_status = "update_service_status_of_current_admin";
      wpcal_request[action_update_status] = {
        service_id: service.id,
        status: status
      };

      let action_get_services = "get_services_of_current_admin";
      wpcal_request[action_get_services] = {
        dummy__: ""
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          if (
            response.data &&
            response.data[action_update_status].hasOwnProperty("status")
          ) {
            //alert(response.data[action_update_status].status);
          }
          if (
            response.data &&
            response.data[action_get_services].hasOwnProperty("service_list")
          ) {
            this.service_list = response.data[action_get_services].service_list;
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    this.load_page();
  }
};
</script>
