                  >
                    <div class="txt-h5">
                      {{ tp_account.provider | tp_provider_name }}
                      <a
                        style="color:#e84653; float:right;"
                        @click="show_disconnect_confirmation(tp_account)"
                        @mouseover="
                          set_mouseover_on_delete(
                            'disconnect_highlight_tp_account_index',
                            index,
                            true
                          )
                        "
                        @mouseleave="
                          set_mouseover_on_delete(
                            'disconnect_highlight_tp_account_index',
                            index,
                            false
                          )
                        "
                        >Disconnect</a
                      >
                    </div>
                    <div class="txt-help">
                      {{ tp_account.tp_account_email }}
                    </div>
                  </div>
                </div>
                <button
                  class="wpc-btn secondary md full-width"
                  @click="show_add_tp_account_list = true"
                  v-if="!show_add_tp_account_list"
                  :disabled="!enable_add_new_tp_account"
                  :title="
                    !enable_add_new_tp_account
                      ? 'You have added all available apps.'
                      : ''
                  "
                >
                  + Add Integration
                </button>
                <div v-if="show_add_tp_account_list">
                  <div class="txt-h5 caps">Select your integration account</div>
                  <ul class="selector block compact location">
                    <li
                      v-for="(tp_account_add_item,
                      index2) in tp_accounts_add_list_final"
                      :key="index2"
                    >
                      <label
                        @click="add_tp_account(tp_account_add_item.provider)"
                        :class="'location_' + tp_account_add_item.provider"
                        ><div class="txt-h5">
                          {{ tp_account_add_item.provider | tp_provider_name }}
                        </div>
                        <div class="txt-help" style="padding-top:0;">
                          Web conferencing
                        </div></label
                      >
