<script>
import SettingsMenu from "@/components/admin/SettingsMenu.vue";

import axios from "axios";

export default {
  components: {
    SettingsMenu
  },
  data() {
    return {
      document_title: "Integrations ‹ Settings",
      show_add_tp_account_list: false,
      tp_accounts: [],
      tp_accounts_add_list: [
        { provider: "zoom_meeting", limit: 1, count: 0, show: true },
        { provider: "gotomeeting_meeting", limit: 1, count: 0, show: true }
      ],
      disconnect_highlight_tp_account_index: null
    };
  },
  computed: {
    tp_accounts_add_list_with_stats() {
      let list = this.clone_deep(this.tp_accounts_add_list);
      for (let index in this.tp_accounts) {
        let tp_account = this.tp_accounts[index];

        let list_index = list.findIndex(list_item => {
          return list_item.provider === tp_account.provider;
        });
        if (list_index != undefined) {
          list[list_index].count++;
          if (list[list_index].limit <= list[list_index].count) {
            list[list_index].show = false;
          }
        }
      }
      return list;
    },
    tp_accounts_add_list_final() {
      let list = [];
      for (let index in this.tp_accounts_add_list_with_stats) {
        if (this.tp_accounts_add_list_with_stats[index].show)
          list.push(this.tp_accounts_add_list_with_stats[index]);
      }
      return list;
    },
    enable_add_new_tp_account() {
      let is_enable = this.tp_accounts_add_list_final.length > 0;
      return is_enable;
    }
  },
  methods: {
    add_tp_account(provider) {
      if (provider === "zoom_meeting" || provider === "gotomeeting_meeting") {
        window.location =
          "admin.php?page=wpcal_admin&wpcal_action=add_tp_account&provider=" +
          provider;
      }
    },
    load_page() {
      let action = "get_tp_accounts_of_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        dummy__: ""
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          Object.assign(this.$data, this.$options.data.apply(this)); //re-initilize data in Vue - this required as we are using this function to after update
          //console.log(response);
          if (
            response.data &&
            response.data[action].hasOwnProperty("tp_accounts")
          ) {
            this.tp_accounts = response.data[action].tp_accounts;
          }

          this.check_auth_for_tp_accounts();
        })
        .catch(error => {
          console.log(error);
        });
    },
    show_disconnect_confirmation(tp_account, force = false) {
      let dialog_text;
      let yes_button_title;

      if (force) {
        dialog_text =
          "Oops! Error while disconnecting the account. <br>Do want to <strong>force-disconnect</strong> the <br>";
        yes_button_title = "Yes, force-disconnect it.";
      } else {
        dialog_text = "Are you sure you want to disconnect the <br>";
        yes_button_title = "Yes, disconnect it.";
      }
      dialog_text +=
        "<strong>" +
        this.$options.filters.tp_provider_name(tp_account.provider) +
        " - " +
        tp_account.tp_account_email +
        "</strong> account?";
      this.$modal.show("dialog", {
        title: "Alert!",
        text: dialog_text,
        buttons: [
          {
            title: yes_button_title,
            handler: () => {
              this.disconnect_tp_account_by_id(tp_account, force);
              this.$modal.hide("dialog");
            }
          },
          {
            title: "No, Cancel.",
            default: true // Will be triggered by default if 'Enter' pressed.
          }
        ]
      });
    },
    disconnect_tp_account_by_id(tp_account, force = false) {
      let action = "disconnect_tp_account_by_id_for_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        tp_account_id: tp_account.id,
        provider: tp_account.provider,
        force: force === true ? "force_remove" : ""
      };

      let action_get_tp_accounts = "get_tp_accounts_of_current_admin";
      wpcal_request[action_get_tp_accounts] = "dummy__";

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          this.notify_single_action_result(response.data[action], {
            success_msg: "Account is disconnected successfully."
          });

          if (
            !force &&
            response.data[action] &&
            response.data[action].hasOwnProperty("status") &&
            response.data[action].status == "error"
          ) {
            setTimeout(() => {
              this.show_disconnect_confirmation(tp_account, true);
            }, 1000);
          }

          if (
            response.data &&
            response.data[action_get_tp_accounts].hasOwnProperty("tp_accounts")
          ) {
            this.tp_accounts =
              response.data[action_get_tp_accounts].tp_accounts;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    check_auth_for_tp_accounts() {
      let wpcal_request = {};
      let action_check_auth =
        "check_auth_if_fails_remove_tp_accounts_for_current_admin";
      wpcal_request[action_check_auth] = "dummy__";

      let action_get_tp_accounts = "get_tp_accounts_of_current_admin";
      wpcal_request[action_get_tp_accounts] = "dummy__";

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data, {
          params: { _remove_options_before_call: { background_call: true } }
        })
        .then(response => {
          //console.log(response);
          if (
            response.data[action_check_auth] &&
            response.data[action_check_auth].hasOwnProperty("status") &&
            response.data[action_check_auth].status == "error"
          ) {
            this.notify_single_action_result(response.data[action_check_auth], {
              success_msg: "Account checked successfully.",
              timeout: false
            });
          }

          if (
            response.data &&
            response.data[action_get_tp_accounts].hasOwnProperty("tp_accounts")
          ) {
            this.tp_accounts =
              response.data[action_get_tp_accounts].tp_accounts;
          }
        })
        .catch(error => {
          console.log(error);
        });
    }

    // sync_all_tp_api() {
    //   //current admin only

    //   //postpone manage_background_task
    //   this.$store.dispatch("manage_background_task", {
    //     initial_time_out: 30000 //30 secs
    //   });

    //   let action = "sync_all_tp_api_for_current_admin";
    //   let wpcal_request = {};
    //   wpcal_request[action] = "dummy__";

    //   let post_data = {
    //     action: "wpcal_process_admin_ajax_request",
    //     wpcal_request: wpcal_request
    //   };

    //   axios
    //     .post(window.wpcal_ajax.ajax_url, post_data, {
    //       params: { _remove_options_before_call: { background_call: true } }
    //     })
    //     .then(response => {
    //       this.load_page();
    //     })
    //     .catch(error => {
    //       console.log(error);
    //     });
    // }
  },
  mounted() {
    this.load_page();

    // setTimeout(() => {
    //   this.sync_all_tp_api(); //Improve later - might create freq calls - should be rare admins visiting this page
    // }, 1000);
  }
};
</script>

