                >
                  <!-- :disabled="!enable_add_new_calendar_account"
                  :title="
                    !enable_add_new_calendar_account
                      ? '6 calendar accounts is max limit'
                      : ''
                  " -->
                  + Add Calendar Account
                </button>
                <div v-if="show_add_calandar_account_list">
                  <div class="txt-h5 caps">Select your calendar account</div>
                  <ul class="selector block cal_apps">
                    <li>
                      <label
                        @click="add_calendar_account('google_calendar')"
                        class="google_calendar"
                        >Google Calendar</label
                      >
                    </li>
                    <li class="disabled">
                      <label class="office365_calendar">
                        <em>Office 365</em>
                        <span>COMING SOON IN PRO</span>
