                    </li>
                    <li class="disabled">
                      <label class="icloud_calendar">
                        <em>iCloud Calendar</em>
                        <span>COMING SOON IN PRO</span>
                      </label>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col">
          <div class="form">
            <div class="form-row">
              <label>Configuration</label>
              <ul class="mbox" style="margin-top: 0;">
                <li>
                  <div>
                    <div class="txt-h5 caps">Add bookings to...</div>
                    <div class="txt-help">
                      Set the calendar you would like to add new bookings to as
                      they're scheduled.
                    </div>
                    <div
                      class="cal-list cf p10 border-all border-round mt10"
                      @click="show_choose_add_bookings_to_calendar_modal"
                    >
                      <div
                        v-if="
                          calendar_accounts.hasOwnProperty(
                            add_bookings_to_calendar_account_id
                          )
                        "
                      >
                        Add to
                        <span
                          class="cal-provider"
                          :class="
                            calendar_accounts[
                              add_bookings_to_calendar_account_id
                            ].provider
                          "
                        >
                          {{
                            calendar_accounts[
                              add_bookings_to_calendar_account_id
                            ].account_email
                          }}<a class="fl-rt">EDIT</a></span
                        >
                        <ul class="bull">
                          <li>{{ add_bookings_to_calendar.name }}</li>
                        </ul>
                      </div>
                      <div v-else>
                        No calendar is choosen.<a class="fl-rt">EDIT</a>
                      </div>
                    </div>

                    <modal
                      name="choose_add_bookings_to_calendar_modal"
                      @before-close="
                        can_show_choose_add_bookings_to_calendar_modal = false
                      "
                      height="auto"
                      width="360px"
                      style="z-index:9999;"
                    >
                      <div
                        class="mbox bg-light shadow mb0"
                        v-if="can_show_choose_add_bookings_to_calendar_modal"
                      >
                        <div class="form">
                          <div class="form-row">
                            <label style="text-align:center;"
                              >Which calendar should we add<br />new events
                              to?</label
                            >
                            <ul class="selector block mt10 add-to-cal">
                              <li
                                v-for="(calendar_account,
                                index) in calendar_accounts"
                                :key="index"
                              >
                                <input
                                  type="radio"
                                  :id="'add-to-cal-opt' + index"
                                  name="add_bookings_to_calendar_account"
                                  :value="calendar_account.id"
                                  v-model="
                                    tmp_edit_add_bookings_to_calendar_account_id
                                  "
                                />
                                <label
                                  :for="'add-to-cal-opt' + index"
                                  :class="calendar_account.provider"
                                >
                                  {{ calendar_account.account_email }}</label
                                >
                              </li>

                              <li>
                                <input
                                  type="radio"
                                  id="add-to-cal-dont"
                                  name="add_bookings_to_calendar_account"
                                  value="no"
                                  v-model="
                                    tmp_edit_add_bookings_to_calendar_account_id
                                  "
                                />
                                <label for="add-to-cal-dont"
                                  >Do not add new bookings to any
                                  calendar</label
                                >
                              </li>
                            </ul>
                          </div>
                        </div>

                        <div
                          v-if="
                            calendar_accounts.hasOwnProperty(
                              tmp_edit_add_bookings_to_calendar_account_id
                            )
                          "
                        >
                          <div style="font-size: 14px; margin: 20px 0 5px;">
                            Add to Calendar
                          </div>
                          <select
                            v-model="tmp_edit_add_bookings_to_calendar_id"
                          >
                            <option
                              v-for="(calendar, index) in this
                                .calendar_accounts[
                                tmp_edit_add_bookings_to_calendar_account_id
                              ].calendars"
                              :key="index"
                              :value="calendar.id"
                              :disabled="calendar.is_writable == '0'"
                              >{{ calendar.name }}</option
                            >
                          </select>
                        </div>
                        <button
                          type="button"
                          class="wpc-btn primary md mt20"
                          @click="update_add_bookings_to_calendar_id"
                        >
                          Update
                        </button>
                      </div>
                    </modal>
                  </div>
                </li>
                <li>
                  <div>
                    <div class="txt-h5 caps">Check for conflicts</div>
                    <div class="txt-help">
                      Set the calendar(s) to check for conflicts to prevent
                      double bookings.
                    </div>
                    <div>
                      <div
                        class="cal-list cf p10 border-all border-round mt10"
                        @click="
                          conflict_calendar_account_id = calendar_account.id;
                          show_choose_conflict_calendars_modal();
                        "
                        v-for="(calendar_account,
                        index) in conflict_calendar_accounts"
                        :key="index"
                      >
                        <div>
                          Check
                          <span
                            class="cal-provider"
                            :class="calendar_account.provider"
                          >
                            {{ calendar_account.account_email
                            }}<a class="fl-rt">EDIT</a></span
                          >
                          <div
                            v-for="(calendar,
                            index2) in calendar_account.calendars"
                            :key="index2"
                          >
                            <ul class="bull">
                              <li>{{ calendar.name }}</li>
                            </ul>
                          </div>
                        </div>
                      </div>
                      <div
                        class="cal-list cf p10 border-all border-round mt10"
                        v-if="_isEmpty(conflict_calendar_accounts)"
                        @click="show_choose_conflict_calendars_modal"
                      >
                        <div>
                          No calendar is choosen. <a class="fl-rt">EDIT</a>
                        </div>
                      </div>
                    </div>

                    <modal
                      name="choose_conflict_calendars_modal"
                      @before-close="
                        can_show_choose_conflict_calendars_modal = false
                      "
                      height="auto"
                      width="360px"
                      style="z-index:9999;"
                    >
                      <div
                        class="mbox bg-light shadow"
                        v-if="can_show_choose_conflict_calendars_modal"
                      >
                        <div class="form">
                          <div class="form-row">
                            <label class="mb10" style="text-align:center;"
                              >Check for conflicts in...</label
                            >
                            <select
                              v-if="calendar_accounts"
                              v-model="tmp_edit_conflict_calendar_account_id"
                            >
                              <option
                                v-for="(calendar_account, index) in this
                                  .calendar_accounts"
                                :key="index"
                                :value="calendar_account.id"
                                >{{ calendar_account.account_email }}</option
                              >
                            </select>

                            <div style="font-size: 14px; margin: 20px 0 5px;">
                              Check these calendars for conflicts
                            </div>

                            <div v-if="tmp_edit_conflict_calendar_account_id">
                              <ul class="selector block mt10 check-conflicts">
                                <li
                                  v-for="(calendar, index) in calendar_accounts[
                                    tmp_edit_conflict_calendar_account_id
                                  ].calendars"
                                  :key="index"
                                >
                                  <input
                                    type="checkbox"
                                    :id="'check-conflicts-opt' + index"
                                    :value="calendar.id"
                                    v-model="tmp_edit_conflict_calendar_ids"
                                  />
                                  <label :for="'check-conflicts-opt' + index">{{
                                    calendar.name
                                  }}</label>
                                </li>
                              </ul>
                            </div>

                            <button
                              type="button"
                              class="wpc-btn primary md mt20"
                              @click="update_conflict_calendar_ids"
                            >
                              Update
                            </button>
                          </div>
                        </div>
                      </div>
                    </modal>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <v-dialog />
  </div>
</template>

<script>
import SettingsMenu from "@/components/admin/SettingsMenu.vue";

import axios from "axios";

export default {
  components: {
    SettingsMenu
  },
  data() {
    return {
      document_title: "Calendars ‹ Settings",
      show_add_calandar_account_list: false,
      calendar_accounts: [],
      can_show_choose_add_bookings_to_calendar_modal: false,
      can_show_choose_conflict_calendars_modal: false,
      add_bookings_to_calendar_account_id: "no",
      add_bookings_to_calendar_id: "",
      tmp_edit_add_bookings_to_calendar_account_id: "no",
      tmp_edit_add_bookings_to_calendar_id: "",
      conflict_calendar_ids: [],
      conflict_calendar_account_id: "",
      tmp_edit_conflict_calendar_ids: [],
      tmp_edit_conflict_calendar_account_id: "",
      disconnect_highlight_calendar_account_index: null
    };
  },
  computed: {
    add_bookings_to_calendar() {
      if (
        this.add_bookings_to_calendar_account_id &&
        this.add_bookings_to_calendar_id
      ) {
        if (
          this.calendar_accounts.hasOwnProperty(
            this.add_bookings_to_calendar_account_id
          ) &&
          this.calendar_accounts[
            this.add_bookings_to_calendar_account_id
          ].calendars.hasOwnProperty(this.add_bookings_to_calendar_id)
        ) {
          return this.calendar_accounts[
            this.add_bookings_to_calendar_account_id
          ].calendars[this.add_bookings_to_calendar_id];
        }
      }
      return {};
    },
    conflict_calendar_accounts() {
      let conflict_calendar_accounts = {};
      for (let calendar_account_id in this.calendar_accounts) {
        for (let calendar_id in this.calendar_accounts[calendar_account_id]
          .calendars) {
          if (this.conflict_calendar_ids.indexOf(calendar_id) !== -1) {
            if (
              !conflict_calendar_accounts.hasOwnProperty(calendar_account_id)
            ) {
              conflict_calendar_accounts[calendar_account_id] = Object.assign(
                {},
                this.calendar_accounts[calendar_account_id]
              );
              conflict_calendar_accounts[calendar_account_id].calendars = {};
            }
            conflict_calendar_accounts[calendar_account_id].calendars[
              calendar_id
            ] = this.calendar_accounts[calendar_account_id].calendars[
              calendar_id
            ];
          }
        }
      }
      return conflict_calendar_accounts;
    },
    enable_add_new_calendar_account() {
      // let count = Object.keys(this.calendar_accounts).length;
      // if (count >= 6) {
      //   return false;
      // }
      return true;
    }
  },
  methods: {
    add_calendar_account(provider) {
      if (provider === "google_calendar") {
        window.location =
          "admin.php?page=wpcal_admin&wpcal_action=add_calendar_account&provider=google_calendar";
      }
    },
    load_page() {
      let action = "get_calendar_accounts_details_of_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        dummy__: ""
      };

      let action2 = "get_add_bookings_to_calendar_of_current_admin";
      wpcal_request[action2] = {
        dummy__: ""
      };

      let action3 = "get_conflict_calendar_ids_of_current_admin";
      wpcal_request[action3] = {
        dummy__: ""
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          Object.assign(this.$data, this.$options.data.apply(this)); //re-initilize data in Vue - this required as we are using this function to after update
          //console.log(response);
          if (
            response.data &&
            response.data[action].hasOwnProperty("calendar_accounts_details")
          ) {
            this.calendar_accounts =
              response.data[action].calendar_accounts_details;
            if (
              typeof response.data[action2] == "object" &&
              response.data[action2].hasOwnProperty(
                "add_bookings_to_calendar"
              ) &&
              response.data[action2].add_bookings_to_calendar &&
              response.data[action2].add_bookings_to_calendar.hasOwnProperty(
                "calendar_id"
              )
            ) {
              this.add_bookings_to_calendar_id =
                response.data[action2].add_bookings_to_calendar.calendar_id;
              this.add_bookings_to_calendar_account_id =
                response.data[
                  action2
                ].add_bookings_to_calendar.calendar_account_id;
            }
            this.conflict_calendar_ids =
              response.data[action3].conflict_calendar_ids;
            this.set_default_conflict_calendar_account_id();
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    update_add_bookings_to_calendar_id() {
      let add_bookings_to_calendar_id = this
        .tmp_edit_add_bookings_to_calendar_id;
      if (this.tmp_edit_add_bookings_to_calendar_account_id == "no") {
        add_bookings_to_calendar_id = "no";
      }
      let action = "update_add_bookings_to_calendar_id_for_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        add_bookings_to_calendar_id: add_bookings_to_calendar_id
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          this.notify_single_action_result(response.data[action], {
            success_msg: "Settings saved."
          });
          if (response.data && response.data[action].hasOwnProperty("status")) {
            this.load_page();
          }
        })
        .catch(error => {
          console.log(error);
        });
      this.$modal.hide("choose_add_bookings_to_calendar_modal");
    },
    update_conflict_calendar_ids() {
      let action = "update_conflict_calendar_ids_for_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        conflict_calendar_ids: this.tmp_edit_conflict_calendar_ids,
        conflict_calendar_ids_length: this.tmp_edit_conflict_calendar_ids.length
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          this.notify_single_action_result(response.data[action], {
            success_msg: "Settings saved."
          });
          if (response.data && response.data[action].hasOwnProperty("status")) {
            this.load_page();
            if (response.data && response.data[action].status === "success") {
              this.sync_all_calendar_api();
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
      this.$modal.hide("choose_conflict_calendars_modal");
    },
    disconnect_calendar_by_id(calendar_account) {
      let action = "disconnect_calendar_by_id_for_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        calendar_account_id: calendar_account.id,
        provider: calendar_account.provider
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          this.notify_single_action_result(response.data[action], {
            success_msg: "Calendar is disconnected successfully."
          });
          // if (response.data && response.data[action].hasOwnProperty("status")) {
          // }
          this.load_page();
        })
        .catch(error => {
          console.log(error);
        });
    },
    show_disconnect_confirmation(calendar_account) {
      let dialog_text = "Are you sure you want to disconnect the <br>";
      dialog_text +=
        "<strong>" + calendar_account.account_email + "</strong> account?";
      this.$modal.show("dialog", {
        title: "Alert!",
        text: dialog_text,
        buttons: [
          {
            title: "Yes, disconnect it.",
            handler: () => {
              this.disconnect_calendar_by_id(calendar_account);
              this.$modal.hide("dialog");
            }
          },
          {
            title: "No, Cancel.",
            default: true // Will be triggered by default if 'Enter' pressed.
          }
        ]
      });
    },
    show_choose_add_bookings_to_calendar_modal() {
      this.tmp_edit_add_bookings_to_calendar_account_id = this.add_bookings_to_calendar_account_id;
      this.tmp_edit_add_bookings_to_calendar_id = this.add_bookings_to_calendar_id;
      this.can_show_choose_add_bookings_to_calendar_modal = true;
      this.$modal.show("choose_add_bookings_to_calendar_modal");
    },
    show_choose_conflict_calendars_modal() {
      this.tmp_edit_conflict_calendar_account_id = this.conflict_calendar_account_id;
      this.tmp_edit_conflict_calendar_ids = this.conflict_calendar_ids;
      this.can_show_choose_conflict_calendars_modal = true;
      this.$modal.show("choose_conflict_calendars_modal");
    },
    set_default_conflict_calendar_account_id() {
      for (let calendar_account_id in this.calendar_accounts) {
        if (this.conflict_calendar_ids.length == 0) {
          this.conflict_calendar_account_id = calendar_account_id;
          break;
        }

        for (let calendar_id in this.calendar_accounts[calendar_account_id]
          .calendars) {
          if (this.conflict_calendar_ids.indexOf(calendar_id) !== -1) {
            this.conflict_calendar_account_id = calendar_account_id;
            return;
          }
        }
      }
    },
    sync_all_calendar_api() {
      //current admin only

      //postpone manage_background_task
      this.$store.dispatch("manage_background_task", {
        initial_time_out: 30000 //30 secs
      });

      let action = "sync_all_calendar_api_for_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = "dummy__";

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data, {
          params: { _remove_options_before_call: { background_call: true } }
        })
        .then(response => {
          this.load_page();
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    this.load_page();

    setTimeout(() => {
      this.sync_all_calendar_api(); //Improve later - might create freq calls - should be rare admins visiting this page
    }, 1000);
  }
};
</script>

<style src="@/assets/css/admin_settings.css"></style>
