<template>
  <div>
    <ul class="selector block apply-multiple-days">
      <li v-for="(day, index) in weekdays" :key="index">
        <input
          type="checkbox"
          :id="'multiple-' + day.index"
          name="apply-multiple-days"
          :value="day.index"
          v-model="selected_days"
          @change="$emit('change_prop_selected_days', selected_days)"
        />
        <label :for="'multiple-' + day.index">{{ day.full_day }}</label>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  model: {
    prop: "prop_selected_days",
    event: "change_prop_selected_days"
  },
  props: {
    prop_selected_days: {
      type: Array
    }
  },
  data() {
    return {
      weekdays: [
        { index: 7, full_day: "Sunday" },
        { index: 1, full_day: "Monday" },
        { index: 2, full_day: "Tuesday" },
        { index: 3, full_day: "Wednesday" },
        { index: 4, full_day: "Thursday" },
        { index: 5, full_day: "Friday" },
        { index: 6, full_day: "Saturday" }
      ],
      selected_days: []
    };
  },
  watch: {
    prop_selected_days(new_value) {
      this.selected_days = new_value;
    }
  },
  mounted() {
    this.selected_days = this.prop_selected_days;
  }
};
</script>
